import {
  BootstrapTable,
  TableHeaderColumn,
} from 'react-bootstrap-table';
import '../node_modules/react-bootstrap-table/dist/react-bootstrap-table-all.min.css';
import React from 'react';
import { Component } from 'react';

const products = [
  {
    id: 0,
    name: 'Banana',
    company: 'ss',
    country: 'sssss',
    operation: 'sss',
  },
  {
    id: 1,
    name: 'Banaxjxxnna',
    company: 'ss',
    country: 'sssss',
    operation: 'sss',
  },
  {
    id: 2,
    name: 'Banjdjdjdjana',
    company: 'ss',
    country: 'sssss',
    operation: 'sss',
  },
];

const cellEditProp = {
  mode: 'click',
  blurToSave: true,
};

class ExpandRow extends Component {
  isExpandableRow = (row) => {
    if (row.id < 3) return true;
    return false;
  };


  expandComponent = row => (
    <div>
      <p>
This Expand row is belong to rowKey $
        {row.id}

        {' '}
      </p>
    </div>
  )

 

  render() {
    const options = {
      expandRowBgColor: 'red',
      expandBy: 'column', // Currently, available value is row and column, default is row
    };
    return (
      <BootstrapTable
      data={products}
      options={options}
      expandableRow={this.isExpandableRow}
      expandComponent={this.expandComponent}
      cellEdit={cellEditProp}
    >
      <TableHeaderColumn dataField="id"  className='td-header-string-example' isKey>
          Product ID
      </TableHeaderColumn>
      <TableHeaderColumn dataField="name" expandable={false}>
          Product Name
      </TableHeaderColumn>
      <TableHeaderColumn dataField="company" expandable={false} editable={{ type: 'textarea' }}>
          Company
      </TableHeaderColumn>
      <TableHeaderColumn dataField="country" expandable={false}>
          Country
      </TableHeaderColumn>
      <TableHeaderColumn dataField="operation" expandable={false}>
          operation
      </TableHeaderColumn>
    </BootstrapTable>
    );
  }
}

export default ExpandRow;
